import Promise from 'bluebird';
import BaseDriver from './_base';
import bus from '../../bus';
import { sessionTimer } from '../../lib';
import { DOM, format, log, request, requestTypes } from '../../../utils';
import { extractData } from '../../../utils/DOM';

const { extractElement, extractFormValues } = DOM;
const { elemToFloat, toFloat } = format;

const CART_EMPTY_SELECTOR = '.checkout-itemNum-js';
const IS_ON_CART = window.location.href.match(/OrderItemDisplay/i);
const REQUEST_URL = 'https://www.timberland.com/webapp/wcs/stores/servlet';

/* Driver behavior:
	+ Existing code: yes
	+ Remove code: yes
	+ Code retesting: yes
	+ Stackable: no
	+ Free gift support: no
	+ isValid implemented: no
	+ Manually apply code: no
	+ Show message about free shipping: no
	+ Driver is SPA: yes, on cart & billing checkout step (if billing address is saved)
	+ Gather merchant codes: no
	+ RestoreUserData: no
====Special case====
	+ Some codes may restore shipping method to default, we mark that codes as inValid
	+ Session timer set for 15 min.
	+ Sometimes server can redirect to /webapp/wcs/stores/servlet/OrderItemDisplay
	  but sometimes can redirect to /shop/OrderItemDisplay.
	+ Domain doesn't change and another requests(applying/removing codes) works as well
	+ may be some little mistake with total calculation on site's UI.
*/

const Driver = class Driver extends BaseDriver {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '#amount-after-giftcards-apply-summary';
		this.EXISTING_CODE_SELECTOR = '.checkout-gift-card-num';
		this.APPLY_CODE_FORM_SELECTOR = 'form[action=VFAjaxPromotionCodeManage]';
		this.REMOVE_CODE_FORM_SELECTOR = '.checkout-summary-promo-code-remove';
		this.GOODS_SELECTOR = '.billing-info-form, .product-list-container';
		this.CALCULATE_FORM_SELECTOR = 'form[name=reCalculateOrderForm]';
		this.CHECKOUT_FORM_SELECTOR = '[name="refreshTotalForm"]';
		this.TAX_SELECTOR = '.checkout-estimate-tax';
		this.SHIPPING_METHOD_SELECTOR = '.checkout-ship-mode-id';
		this.SHIPPING_SELECTOR = '.checkout-estimate-shipping-2';
		this.SHIPPING_UPDATE_SEL = 'form[name=shipModeUpdateForm]';
		this.PICKUP_PAGE_SELECTOR = '[title="Ship to Home"]';
		this.BILLING_INPUT_SELECTOR = '.address-form-first-name-label';
		this.HEADERS = {
			headers: {
				'x-requested-with': 'XMLHttpRequest',
				'content-type': 'application/x-www-form-urlencoded; charset=UTF-8',
			},
		};
		sessionTimer.initialize({});
		request.setType(requestTypes.embedded);
	}

	checkCondition() {
		const isCartEmpty =
			document.querySelector(CART_EMPTY_SELECTOR) &&
			document.querySelector(CART_EMPTY_SELECTOR).textContent === '0';
		const isSummary = document.querySelector(this.TOTAL_SELECTOR);
		const savedBillingAddress = !(document.querySelector(this.BILLING_INPUT_SELECTOR) || {}).offsetParent;
		const rightUrl = IS_ON_CART || (window.location.href.includes('BillingAndPaymentView') && savedBillingAddress);
		const goodsInCart = document.querySelector(this.GOODS_SELECTOR);

		return !isCartEmpty && isSummary && rightUrl && goodsInCart;
	}

	beforeTestCodes() {
		if (IS_ON_CART && !document.querySelector(this.PICKUP_PAGE_SELECTOR)) {
			this.SHIPPING_METHOD_ID = this._getShippingMethodId();
			this.shippingUpdateParams = extractFormValues(this.SHIPPING_UPDATE_SEL, false, true);
			[...document.querySelectorAll('.checkout-item-table-item > input')].map(item =>
				this.shippingUpdateParams.params.set(item.name, item.value),
			);
			const orderItemIds = extractData(this.SHIPPING_METHOD_SELECTOR, false, false, 'data-orderitemids').split(
				',',
			);
			orderItemIds.forEach((item, index) => {
				this.shippingUpdateParams.params.set(`orderItemId_${index + 1}`, item);
				this.shippingUpdateParams.params.set(`shipModeId_${index + 1}`, this.SHIPPING_METHOD_ID);
			});
			this.shippingUpdateParams.params.set('requesttype', 'ajax');
			this.shippingUpdateParams.params.delete('catalogId_1');
		}

		this.calculateRequestParams = extractFormValues(this.CALCULATE_FORM_SELECTOR, false, true);
		this.updateCheckoutParams = extractFormValues(this.CHECKOUT_FORM_SELECTOR);
		this.updateCheckoutParams.params.set('requesttype', 'ajax');

		this.applyForm = extractFormValues(this.APPLY_CODE_FORM_SELECTOR);
		this.applyForm.params.set('taskType', 'A');
		this.applyForm.params.delete('codeApplied');
		this.applyForm.params.set('requesttype', 'ajax');

		this.removeForm = extractFormValues(this.REMOVE_CODE_FORM_SELECTOR);
		this.removeForm.params.set('taskType', 'R');
		this.removeForm.params.delete('refresh');
		this.removeForm.params.set('requesttype', 'ajax');
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.EXISTING_CODE_SELECTOR)].map(element => element.textContent);

		return {
			codes,
			codeCount: codes.length,
		};
	}

	removeCodes(codes) {
		return this._removeCode(codes[0])
			.then(() => this._getOrderCalculate())
			.then(responseText => this._parseTotal(responseText));
	}

	checkCodes(codes, originalPrice = this._parseTotal()) {
		log(`Orginal price: ${originalPrice}`);
		return Promise.each(codes, code =>
			this._applyCode(code).then(responseJSON => {
				const result = {
					code,
					finalPrice: originalPrice,
					finalDiscount: 0,
					isValid: true,
				};
				if (responseJSON.errorMessage) {
					result.isValid = false;
					return this._removeCode(code).then(() => bus.$emit('code-checked', result));
				}
				return this._getOrderCalculate().then(responseText => {
					if (
						IS_ON_CART &&
						!document.querySelector(this.PICKUP_PAGE_SELECTOR) &&
						this._getShippingMethodId(responseText) !== this.SHIPPING_METHOD_ID
					) {
						result.isValid = false;
						return this._removeCode(code)
							.then(() => this._updateShipping())
							.then(() => bus.$emit('code-checked', result));
					}
					result.finalPrice = this._parseTotal(responseText);
					result.finalDiscount = originalPrice - result.finalPrice;

					return this._removeCode(code).then(() => bus.$emit('code-checked', result));
				});
			}),
		);
	}

	_getShippingMethodId(data = false) {
		return extractElement(this.SHIPPING_METHOD_SELECTOR, data).value; // doesn't work wit getAttribute method
	}

	_updateShipping() {
		return request.post(`${REQUEST_URL}/VFAjaxOrderItemUpdate`, this.shippingUpdateParams.params, this.HEADERS);
	}

	getPreTaxShippingTotal() {
		return this._getOrderCalculate().then(response => {
			let tax;
			let shipping;
			if (response && response.amounts) {
				const { amounts } = response;
				tax = toFloat(amounts.totalTax);
				shipping = toFloat(amounts.shippingCharge);
			} else {
				const shippingElement = extractElement(this.SHIPPING_SELECTOR, response);
				shipping = shippingElement && /\d/.test(shippingElement.textContent) ? elemToFloat(shippingElement) : 0;
				tax = elemToFloat(extractElement(this.TAX_SELECTOR, response)) || 0;
			}

			return this._parseTotal(response) - shipping - tax;
		});
	}

	applyCodes(codes) {
		return this._applyCode(codes[0]).then(() => this._getOrderCalculate());
	}

	_applyCode(code) {
		this.applyForm.params.set('promoCode', code);

		return this._manageCode(`${REQUEST_URL}/VFAjaxPromotionCodeManage`, this.applyForm.params);
	}

	_removeCode(code) {
		this.removeForm.params.set('promoCode', code);

		return this._manageCode(`${REQUEST_URL}/AjaxPromotionCodeManage`, this.removeForm.params);
	}

	async _manageCode(url, body) {
		return request.post(url, body, this.HEADERS).then(response => response.json());
	}

	_getOrderCalculate() {
		if (IS_ON_CART) {
			return request
				.get(`${REQUEST_URL}/OrderCalculate?${this.calculateRequestParams.params.toString()}`)
				.then(response => response.text());
		}
		return request
			.post(`${REQUEST_URL}/AjaxOrderCalculate`, this.calculateRequestParams.params, this.HEADERS)
			.then(response => (IS_ON_CART ? response.text() : response.json()));
	}

	_parseTotal(data = false) {
		return data && data.amounts
			? toFloat(data.amounts.grandTotalValue)
			: elemToFloat(extractElement(this.TOTAL_SELECTOR, data));
	}

	_getUpdatedCheckout(retry = false) {
		const url = window.location.href;
		return request
			.get(url, null, {
				headers: {
					'x-requested-with': 'XMLHttpRequest',
				},
			})
			.then(response =>
				response.status === 500 && retry === false ? this._getUpdatedCheckout(true) : response.text(),
			);
	}
};

export default {
	name: 'Timberland',
	domain: 'timberland.com',
	Driver,
	maxCoupons: 6,
	isSinglePageApp: true,
	restoreUserData: true,
};
