import Promise from 'bluebird';
import DriverTemplate from './_base';
import bus from '../../bus';
import { sessionTimer, handleDriverError } from '../../lib';
import { DOM, completionActions, format, log, request, requestTypes, browserUtils } from '../../../utils';

const { wait } = browserUtils;
const { extractData, setValue } = DOM;
const { toFloat, elemToFloat } = format;

const DOMAIN = '1800flowers.com';
const PWA_DOMAIN = `pwa.www.${DOMAIN}`;
const isPWA = window.location.href.includes('pwa');

/*
Driver behavior description:
	Existing code: yes
	Remove code: yes
	Stackable: no
	Free gift support: no
	'isValid' implemented: yes
	Codes can be replaced by another code: no / yes (pwa)
	Page, where extension works:
                 1800flowers: Checkout,
                 pwa.1800flowers: review page
	Is driver SPA and add the condition to show popup: no / yes (pwa)
	sessionTimer 15 min
*/

const NotPwaDriver = class Driver extends DriverTemplate {
	constructor() {
		super(DOMAIN);
		this.PRICES_SELECTOR = '[class*=total-label] [class*=row]';
		this.PRICE_SELECTOR = '[class*=PaddingSpace][class*=col2]';
		this.SHIPPING_REGEX = /"order_shipping": "(.+)/i;
		this.SKIP_CODE_TESTING = false;

		sessionTimer.initialize({
			actionInterval: null,
			timeout: 15,
		});
	}

	checkCodes(codes, originalPrice = this._parseTotal()) {
		log(`Original price: ${originalPrice}`);

		return Promise.each(codes, code => {
			const result = { code, finalPrice: originalPrice, finalDiscount: 0, isValid: false };
			return this._applyCode(code).then(responseJSON => {
				if (this.SKIP_CODE_TESTING) {
					return bus.$emit('code-checked', result);
				}
				if (!this._codeAppliedCheck(responseJSON)) {
					return bus.$emit('code-checked', result);
				}
				return this._getUpdatedCart().then(responseHTML => {
					result.finalPrice = this._parseTotal(responseHTML);
					result.finalDiscount = originalPrice - result.finalPrice;
					result.isValid = true;
					return this._removeCode(code).then(() => bus.$emit('code-checked', result));
				});
			});
		});
	}

	getPreTaxShippingTotal() {
		return this._getUpdatedCart().then(response => {
			const shipping = toFloat(this._getShippingValue(response)) || 0;
			// tax not always present on the page
			const taxEl = this._findElement('Estimated Tax', response);
			const tax = taxEl ? elemToFloat(taxEl) || 0 : 0;
			return this._parseTotal(response) - shipping - tax;
		});
	}

	_getShippingValue(response) {
		return [...extractData('script', response, true)]
			.filter(element => element.match(this.SHIPPING_REGEX))
			.map(element => element.match(this.SHIPPING_REGEX)[1])[0];
	}

	_manageCode(urlString, method, body) {
		return fetch(urlString, { method, credentials: 'include', body })
			.then(response => {
				this.SKIP_CODE_TESTING = false;
				if (response.status !== 200) {
					this.SKIP_CODE_TESTING = true;
					handleDriverError(new Error('Invalid response'), {
						response,
					});
				}
				return response.text();
			})
			.then(response => {
				// Return JSON if it is possible
				try {
					this.lastResponse = JSON.parse(response);
				} catch (e) {
					this.lastResponse = response;
				}

				return this.lastResponse;
			});
	}
};

const PwaDriver = class Driver extends DriverTemplate {
	constructor() {
		super(PWA_DOMAIN);
		this.PROMO_CODE_FORM = '[name="promoCode"]';
		this.PAYMENT_SELECTOR = '[type="radio"]:checked';
		this.MEMBER_SELECTOR = '[name="promoMembershipNumber"]';
		this.REMOVE_BUTTON_SELECTOR = '[aria-label="Remove"]';
		this.APPLY_BUTTON_SELECTOR = '[aria-label="apply"]';
		this.TOTAL_STRING = 'Order Total:';
		this.PROMO_REGEXP = /(\w+) has been applied/;
		request.setType(requestTypes.embedded);
	}

	watchForCartUrl() {
		sessionTimer.initialize({
			actionInterval: super.watchForCartUrl(),
			timeout: 15,
		});
	}

	checkCondition() {
		const rightUrl = window.location.href.includes('/review');
		const isPromoInput = document.querySelector(this.PROMO_CODE_FORM);
		const isRemove = document.querySelector(this.REMOVE_BUTTON_SELECTOR);
		const totalLabelElem = [...document.querySelectorAll('div')].find(el => el.innerText === this.TOTAL_STRING);
		const isTotal = totalLabelElem && totalLabelElem.nextElementSibling;
		return (isPromoInput || isRemove) && rightUrl && isTotal && this._parseTotal();
	}

	beforeTestCodes() {
		const paymentEl = document.querySelector(this.PAYMENT_SELECTOR);
		const memberEl = document.querySelector(this.MEMBER_SELECTOR);
		const userInfo = JSON.parse(localStorage.getItem('@flowers/auth/user'));
		const itemsInfo = JSON.parse(localStorage.getItem('@flowers/cart-v1'));
		const { items, hasPassport } = itemsInfo;
		const changedItems = items.map(el => {
			const { productBase, brandCode } = el.product;
			const { deliveryDate } = el.deliveryDate;

			el.brandCode = brandCode;
			el.productBase = productBase;
			el.deliveryDate = deliveryDate;
			el.deliveryDateFlexOption = el.deliveryDateSelectedFlexOption;
			el.deliveryDateDtwId = el.deliveryDateSelectedDtwOption;

			delete el.ttl;
			delete el.product;
			delete el.productPageSearchParams;
			delete el.addOnsSkus;
			delete el.deliveryDateQuery;
			delete el.deliveryDateSelectedFlexOption;
			delete el.deliveryDateSelectedDtwOption;
			delete el.shippingCountry;
			delete el.personalization;
			return el;
		});

		this.requestData = {
			customerId: userInfo ? userInfo.contactId : '',
			customerType: userInfo ? userInfo.customerType : 'R',
			promoMembershipNumber: memberEl && memberEl.value,
			paymentType: paymentEl && paymentEl.value,
			giftCards: [],
			passport: hasPassport,
			items: changedItems,
		};
	}

	getExistingCodes() {
		const codes = [
			...new Set(
				[...document.querySelectorAll('div')]
					.map(el => el.textContent.match(this.PROMO_REGEXP))
					.filter(Boolean)
					.map(code => code[1]),
			),
		];

		return {
			codes,
			codeCount: codes.length,
		};
	}

	removeCodes() {
		const buttons = document.querySelectorAll(this.REMOVE_BUTTON_SELECTOR);
		buttons[buttons.length - 1].click();
		const total = () => [...document.querySelectorAll('div')].find(el => el.innerText === this.TOTAL_STRING);
		return wait(total).then(() => this._parseTotal());
	}

	checkCodes(codes, originalPrice = this._parseTotal()) {
		log(`Original price: ${originalPrice}`);
		return Promise.each(codes, code => {
			const result = {
				code,
				finalPrice: originalPrice,
				finalDiscount: 0,
				isValid: true,
			};
			return this._applyCode(code).then(responseJSON => {
				if (responseJSON.errors.length) {
					result.isValid = false;
				} else {
					result.finalPrice = this._parseTotal(responseJSON);
					result.finalDiscount = originalPrice - result.finalPrice;
				}
				return bus.$emit('code-checked', result);
			});
		});
	}

	applyCodes(codes) {
		[this.lastCode] = codes;
		return this._manuallyApplyCode(this.lastCode);
	}

	getPreTaxShippingTotal() {
		return this._applyCode(this.lastCode).then(responseJSON => toFloat(responseJSON.totals.merchandise));
	}

	_applyCode(code) {
		this.requestData.promoCode = code;
		return fetch(`https://${PWA_DOMAIN}/api/checkout/order/summary.json`, {
			method: 'POST',
			credentials: 'include',
			headers: {
				'content-type': 'application/json',
			},
			body: JSON.stringify(this.requestData),
		}).then(response => response.json());
	}

	_manuallyApplyCode(code) {
		const inputs = document.querySelectorAll(this.PROMO_CODE_FORM);
		const buttons = document.querySelectorAll(this.APPLY_BUTTON_SELECTOR);
		setValue(inputs[inputs.length - 1], code);
		buttons[buttons.length - 1].click();
		return Promise.resolve();
	}

	_parseTotal(data) {
		if (data) {
			return toFloat(data.totals.total);
		}
		const total = [...document.querySelectorAll('div')].find(el => el.innerText === this.TOTAL_STRING)
			.nextElementSibling;
		return elemToFloat(total);
	}
};

export default {
	name: '1800 Flowers',
	domain: '1800flowers.com',
	isSinglePageApp: isPWA,
	completionAction: isPWA ? completionActions.none : completionActions.revisit,
	Driver: isPWA ? PwaDriver : NotPwaDriver,
	maxCoupons: 9,
	isOnCartUrl: href =>
		isPWA ? false : href.includes(`/FDPlaceOrderDisplayCmd`) && document.querySelector('#promoCode'),
	isCartEmpty() {
		return document.querySelector('.ECP-emptycart');
	},
};
