import Promise from 'bluebird';
import DriverTemplate from './_base';
import bus from '../../bus';
import { format, DOM, log } from '../../../utils';

const { toFloat, elemToFloat } = format;
const { extractFormValues, extractElement } = DOM;

/*
Driver behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: no
+ Free gift support: no
+ Free shipping: no
+ isValid implemented: yes
+ Codes can be replaced by another code: no
+ Manually apply code: no
+
+ Pages, where extension works: cart, payment
+ Is driver SPA yes
+ Gather codes on site pages: no
+ Special case about the driver works.
    - sessionTimer yes (15 minuteus)
*/

const DOMAIN = 'menswearhouse.com';

const Driver = class Driver extends DriverTemplate {
	constructor() {
		super(DOMAIN);

		this.BASE_URL = `https://www.${DOMAIN}`;
		this.API_URL = `${this.BASE_URL}/webapp/wcs/stores/servlet`;

		this.MANAGE_CODE_URL = `${this.API_URL}/AjaxPromotionCodeManage`;
		this.UPDATE_TOTAL_URL = `${this.API_URL}/AjaxOrderChangeServiceItemUpdate`;

		this.CART_URL = `${this.BASE_URL}/AjaxOrderItemDisplayView`;
		this.CHECKOUT_URL = `${this.BASE_URL}/OrderShippingBillingView`;

		this.CODE_INPUT_SELECTOR = '#promoCodeCheckoutInput, #promoCode';
		this.APPLIED_CODE_SELECTOR = '#appliedPromotionCodes .promotionName';
		this.FORM_SELECTOR = '#PromotionCodeForm';
		this.SHIPPING_TAB_SELECTOR = '#tab-shipping.active';

		this.TOTAL_ELEMENT_CONFIG = { selector: '.product-price.total_figures[data-gtm="cart-total"]' };
		this.CHARGES_SELECTOR = '.order-summary-list li';
		this.CHARGES_REGEX = /taxes|shipping/gi;
	}

	checkCondition() {
		const { href } = window.location;
		const codeInput = document.querySelector(this.CODE_INPUT_SELECTOR);
		const isOnCheckout = href.startsWith(this.CHECKOUT_URL);
		const isOnCart = href.startsWith(this.CART_URL);
		const shippingTabActive = document.querySelector(this.SHIPPING_TAB_SELECTOR);
		return codeInput && !shippingTabActive && (isOnCheckout || isOnCart);
	}

	getExistingCodes() {
		const existingCodeElement = document.querySelector(this.APPLIED_CODE_SELECTOR);
		const codes = existingCodeElement ? [existingCodeElement.textContent.trim()] : [];
		return {
			codes,
			codeCount: codes.length,
		};
	}

	beforeTestCodes() {
		this.formData = extractFormValues(this.FORM_SELECTOR);
		this.formData.params.set('requesttype', 'ajax');
	}

	checkCodes(codes, originalPrice = this._parseTotal()) {
		log(`Original price: ${originalPrice}`);

		return Promise.each(codes, code => {
			const result = { code, finalPrice: originalPrice, finalDiscount: 0, isValid: true };
			return this._applyCode(code).then(() => {
				return this._getUpdatedCart().then(responseHTML => {
					result.finalPrice = this._parseTotal(responseHTML);
					result.finalDiscount = originalPrice - result.finalPrice;
					result.isValid = result.finalPrice < originalPrice;
					return this._removeCode(code).then(() => bus.$emit('code-checked', result));
				});
			});
		});
	}

	getPreTaxShippingTotal() {
		return this._getUpdatedCart().then(responseHTML => {
			const total = this._parseTotal(responseHTML);
			const charges = [...extractElement(this.CHARGES_SELECTOR, responseHTML, true)]
				.filter(chargeEl => chargeEl.textContent.match(this.CHARGES_REGEX))
				.map(charge => charge.textContent.match(/(\d+\.\d+)/i))
				.filter(Boolean)
				.map(matched => matched[1])
				.reduce((a, b) => toFloat(a) + toFloat(b), 0);
			return total - charges;
		});
	}

	_applyCode(code) {
		this.formData.params.set('taskType', 'A');
		this.formData.params.set('promoCode', code);
		return this._manageCode(this.MANAGE_CODE_URL, this.formData.params).then(() => this._updateTotal());
	}

	_removeCode(code) {
		this.formData.params.set('taskType', 'R');
		this.formData.params.set('promoCode', code);
		return this._manageCode(this.MANAGE_CODE_URL, this.formData.params).then(() => this._updateTotal());
	}

	_updateTotal() {
		this.formData.params.delete('promoCode');
		this.formData.params.delete('taskType');
		return this._manageCode(this.UPDATE_TOTAL_URL, this.formData.params);
	}

	_getUpdatedCart() {
		return fetch(window.location.href).then(res => res.text());
	}

	_manageCode(url, body) {
		return fetch(url, {
			credentials: 'include',
			headers: {
				'content-type': 'application/x-www-form-urlencoded; charset=UTF-8',
				'x-requested-with': 'XMLHttpRequest',
			},
			referrerPolicy: 'no-referrer-when-downgrade',
			body,
			method: 'POST',
			mode: 'cors',
		}).then(res => res.text());
	}

	_parseTotal(data = false) {
		return elemToFloat(extractElement(this.TOTAL_ELEMENT_CONFIG.selector, data));
	}
};

export default {
	name: "Men's Wearhouse",
	domain: 'menswearhouse.com',
	isSinglePageApp: true,
	Driver,
};
