import DriverTemplate from './_base';
import { sessionTimer } from '../../lib';
import { format, DOM, completionActions } from '../../../utils';

const { elemToFloat } = format;
const { extractElement, extractFormValues } = DOM;

const isCartCheckoutVersion = document.querySelector('.price-cart-summary');

/*
Driver behavior description:
    Existing code: yes
    Remove code: yes
    Stackable: no
    Free gift support: no
    'isValid' implemented: yes
     Codes can be replaced by another code: no
    Manually apply code: no
    Is driver SPA and add the condition to show popup: yes
    sessionTimer 15 min
    pages, where extension works: Cart, Checkout
    Special case: get Total price with shipping,
     so total and final price in console will be wrong, but discount will be right
*/

const DriverCartCheckoutVersion = class Driver extends DriverTemplate {
	constructor() {
		super('1800baskets.com');

		this.TOTAL_SELECTOR = '.price-cart-summary';
		this.REMOVE_FORM_SELECTOR = '#AjaxRESTRemovePromoCodeForWidget';
	}

	beforeTestCodes() {
		this.APPLY_FORM_DATA = extractFormValues(this.APPLY_FORM_SELECTOR);
		this.REMOVE_FORM_DATA = document.querySelector(this.REMOVE_FORM_SELECTOR).getAttribute('value');
		this.UPDATE_FORM_DATA = document.querySelector(this.UPDATE_FORM_SELECTOR).getAttribute('value');
	}

	_getUpdatedCart() {
		return this._manageCode(`${this.BASE_URL}${this.UPDATE_FORM_DATA}`, 'POST');
	}

	_parseTotal(data = false) {
		return elemToFloat(
			extractElement(this.TOTAL_SIBLING_SELECTOR, data).previousElementSibling.querySelector(this.TOTAL_SELECTOR),
		);
	}
};

/*
Driver behavior description:
    Existing code: yes
    Remove code: yes
    Stackable: no
    Free gift support: no
    'isValid' implemented: yes
     Codes can be replaced by another code: no
    Manually apply code: no
    Page, where extension works: Checkout
    Is driver SPA and add the condition to show popup: no
    sessionTimer 15 min
*/
const DriverCheckoutVersion = class Driver extends DriverTemplate {
	constructor() {
		super('1800baskets.com');

		sessionTimer.initialize({
			actionInterval: null,
			timeout: 15,
		});
	}
};

export default {
	name: '1800Baskets',
	domain: '1800baskets.com',
	Driver: isCartCheckoutVersion ? DriverCartCheckoutVersion : DriverCheckoutVersion,
	completionAction: isCartCheckoutVersion ? completionActions.default : completionActions.revisit,
	isSinglePageApp: DriverCartCheckoutVersion,
	isOnCartUrl(href) {
		return (
			href.startsWith(`https://www.1800flowers.com/webapp/wcs/stores/servlet/FDPlaceOrderDisplayCmd`) &&
			document.querySelector('#promoCode')
		);
	},
	isCartEmpty() {
		return document.querySelector('.ECP-emptycart');
	},
};
