import DriverTemplate from './_base';

import { format, DOM } from '../../../utils';

const { elemToFloat } = format;
const { extractFormValues, extractElement } = DOM;

/*
Driver behavior description:
    Existing code: yes
    Remove code: yes
    Stackable: no
    Free gift support: no
    'isValid' implemented: yes
    Codes can be replaced by another code: no
    Manually apply code: no
    Is driver SPA : no
*/

const CART_URL = '/AjaxOrderItemDisplayView';
const EMPTY_CARD_SELECTOR = '.empty-cart-espot';

const Driver = class Driver extends DriverTemplate {
	constructor() {
		super('targetoptical.com');

		this.TOTAL_SELECTOR = '#UtagOrderAmt';
		this.APPLIED_CODES_SELECTOR = '.all-promotion-active .specific_promotion';
		this.MERCHANT_CODES_SELECTOR = 'a[aria-label="glasses discount"]';
		this.MERCHANT_CODES_REGEX = /code:\s(.*)/;
		this.SHIPPING_METHOD_SELECTOR = 'form[aria-label="choose shipping method"] input';
		this.TAX_SELECTOR = 'span.price.tax-cart';
		this.BASE_REQUEST_URL = `${this.BASE_URL}/webapp/wcs/stores/servlet`;
		this.MANAGE_CODE_END_POINT = '/AjaxPromotionCodeManage';
		this.GET_ORDER_ID_END_POINT = '/AjaxOrderChangeServiceItemUpdateTO';
		this.MANAGE_CODE_URL = `${this.BASE_REQUEST_URL}${this.MANAGE_CODE_END_POINT}`;
	}

	beforeTestCodes() {
		this.FORM_DATA = extractFormValues(this.APPLY_FORM_SELECTOR);
	}

	getMerchantCodes() {
		const codeElement = document.querySelector(this.MERCHANT_CODES_SELECTOR);
		const codes = codeElement ? [codeElement.textContent.match(this.MERCHANT_CODES_REGEX)[1]] : [];
		return { codes, codeCount: codes.length };
	}

	getExistingCodes() {
		const code = document.querySelector(this.APPLIED_CODES_SELECTOR);
		const codes = code ? [code.getAttribute('data-promotion')] : [];
		return { codes, codeCount: codes.length };
	}

	getPreTaxShippingTotal() {
		return this._getUpdatedCart().then(response => {
			const shippingMethod = [...extractElement(this.SHIPPING_METHOD_SELECTOR, response, true)].find(
				el => el.checked,
			).id;
			const shippingElement = extractElement(`[for=${shippingMethod}] span`, response);
			const shipping = shippingElement.className.includes('free') ? 0 : elemToFloat(shippingElement);
			const tax = elemToFloat(extractElement(this.TAX_SELECTOR, response));
			return this._parseTotal(response) - tax - shipping;
		});
	}

	_applyCode(code) {
		this.FORM_DATA.params.set('taskType', 'A');
		this.FORM_DATA.params.set('promoCode', code);
		return this._manageCode(this.MANAGE_CODE_URL, 'POST', this.FORM_DATA.params);
	}

	_removeCode(code) {
		this.FORM_DATA.params.set('taskType', 'R');
		this.FORM_DATA.params.set('promoCode', code);
		return this._manageCode(this.MANAGE_CODE_URL, 'POST', this.FORM_DATA.params);
	}

	_codeAppliedCheck(data) {
		return typeof data !== 'string';
	}

	_getOrderId(body) {
		return this._manageCode(`${this.BASE_REQUEST_URL}${this.GET_ORDER_ID_END_POINT}`, 'POST', body);
	}

	_getUpdatedCart() {
		const query = this.FORM_DATA.action.split('?')[1];
		return this._getOrderId(query).then(() =>
			this._manageCode(`${this.BASE_URL}${window.location.pathname}?${query}`, 'GET'),
		);
	}
};

export default {
	name: 'Target Optical',
	domain: 'targetoptical.com',
	Driver,
	isOnCartUrl: () => window.location.href.includes(CART_URL) && !document.querySelector(EMPTY_CARD_SELECTOR),
};
