import Promise from 'bluebird';
import DriverTemplate from './_base';
import bus from '../../bus';
import { format, DOM, log } from '../../../utils';

const { elemToFloat } = format;
const { extractFormValues, extractElement } = DOM;

/** Driver behavior description:
-    Existing code: yes
-    Remove code: yes
-    Stackable: no
-    Free gift support: no
-    'isValid' implemented: no
-    Codes can be replaced by another code: sometimes. One of them takes precedence over the other and can overwhelm them. Otherwise, Codes can't be replaced by another code
-    Manually apply code: no
-    Is driver SPA: yes
-	 maxCoupons: 7, because site is not very fast
-    pages, where extension works: Cart
 */

const Driver = class Driver extends DriverTemplate {
	constructor() {
		super();
		this.TOTAL_ELEMENT_CONFIG = {
			selector: '.js-totalplanprice',
		};
		this.EXISTING_CODE_SELECTOR = '.discount-promo p';
		this.CODE_REGEX = /\((\w+)\)/i;
		this.INPUT_ELEM_SELECTOR = '.sc-promo-code';
		this.EMPTY_CART_SELECTOR = '#WC_EmptyShopCartDisplayf_div_1';
		this.BASE_URL = 'https://shop.totalwireless.com';
		this.REQUEST_URL = `${this.BASE_URL}/webapp/wcs/stores/servlet`;
		this.ERROR_REGEXP = /is not correct/;
		this.FORM_SELECTOR = '#PromotionCodeForm';
	}

	checkCondition() {
		const { href } = window.location;
		const emptyCartElement = document.querySelector(this.EMPTY_CART_SELECTOR);
		const inputEl = document.querySelector(this.INPUT_ELEM_SELECTOR);
		const rightUrl =
			href.includes('AjaxOrderItemDisplayView') ||
			href.includes('AjaxCheckoutDisplayView') ||
			href.includes('TFOrderItemDisplay');

		return rightUrl && inputEl && !emptyCartElement;
	}

	beforeTestCodes() {
		this.bodyData = extractFormValues(this.FORM_SELECTOR);
		this.updateCartParams = new URLSearchParams({
			storeId: this.bodyData.params.get('storeId'),
			catalogId: this.bodyData.params.get('catalogId'),
			langId: this.bodyData.params.get('langId'),
			isBundleOfferFlow: false,
		});
		this.updateCartBodyData = new URLSearchParams({
			objectId: '',
			requesttype: 'ajax',
		});

		return Promise.resolve();
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.EXISTING_CODE_SELECTOR)]
			.map(el => el.textContent.match(this.CODE_REGEX))
			.filter(Boolean)
			.map(el => el[1]);
		return {
			codes,
			codeCount: codes.length,
		};
	}

	checkCodes(codes, originalPrice = this._parseTotal()) {
		log(`Original price: ${originalPrice}`);
		return Promise.each(codes, code =>
			this._applyCode(code)
				.then(() => this._getUpdatedCart())
				.then(responseText => {
					const result = {
						code,
						finalPrice: originalPrice,
						finalDiscount: 0,
					};
					if (!this.ERROR_REGEXP.test(responseText)) {
						result.finalPrice = this._parseTotal(responseText);
						result.finalDiscount = originalPrice - result.finalPrice;
						return this._removeCode(code).then(() => bus.$emit('code-checked', result));
					}
					return bus.$emit('code-checked', result);
				}),
		);
	}

	getPreTaxShippingTotal() {
		return this._getUpdatedCart().then(response => this._parseTotal(response));
	}

	_removeCode(code) {
		return this._manageCode(code, 'R');
	}

	_applyCode(code) {
		return this._manageCode(code, 'A');
	}

	_manageCode(code, type) {
		this.bodyData.params.set('promoCode', code);
		this.bodyData.params.set('taskType', type);
		return this._manageUrl(`${this.REQUEST_URL}/AjaxPromotionCodeManage`, this.bodyData.params);
	}

	_getUpdatedCart() {
		return this._manageUrl(
			`${this.BASE_URL}/shop/ShopCartDisplayView?${this.updateCartParams.toString()}`,
			this.updateCartBodyData,
		);
	}

	_manageUrl(url, data) {
		return fetch(url, {
			method: 'POST',
			credentials: 'include',
			headers: {
				'x-requested-with': 'XMLHttpRequest',
			},
			body: data,
		}).then(response => response.text());
	}

	_parseTotal(data = false) {
		return elemToFloat(extractElement(this.TOTAL_ELEMENT_CONFIG.selector, data));
	}
};

export default {
	name: 'Total Wireless',
	domain: 'totalwireless.com',
	maxCoupons: 7,
	Driver,
	isSinglePageApp: true,
};
