/*    eslint class-methods-use-this: ["error", { "exceptMethods": ["_codeAppliedCheck", ""] }] */
import Promise from 'bluebird';
import BaseDriver from '../base';
import bus from '../../bus';
import { format, DOM, log } from '../../../utils';
import { sessionTimer } from '../../lib';

const { toFloat, elemToFloat } = format;
const { extractFormValues, extractElement } = DOM;

// This template driver is for merchants which are similar to 1800flowers.com
// The only parameter is domain
// These merchants use http://www-03.ibm.com/software/products/en/websphere-commerce
// and can be identified by having a /wcsstore/ path on their site

export default class Driver extends BaseDriver {
	constructor(merchantDomain) {
		super();
		this.APPLIED_CODES_SELECTOR = '#appliedPromoCodes';
		this.TOTAL_SIBLING_SELECTOR = '#citiRewardsMessage';
		this.TOTAL_SELECTOR = '#hdnBillingOrderTotalForm #orderTotalBalance';

		this.SHIPPING_FORM_SELECTOR = '.EditBillingAddressModal';

		this.PRICES_SELECTOR = '.order-summary-table .tr-rows, [class*=total-label] [class*=rows]';
		this.PRICE_SELECTOR = '.price-cart-summary, [class*=PaddingSpace]';

		this.APPLY_BUTTON_SELECTOR = '#divApplyBtnStatic a';
		this.CODE_INPUT_SELECTOR = '#promoCode';
		this.APPLIED_CODE_MSG_SELECTOR = '#appliedPromoMsg';

		this.REMOVE_FORM_SELECTOR = '#AjaxAddOrRemovePromotionOrEGCURL';
		this.APPLY_FORM_SELECTOR = '#PromotionCodeForm';
		this.UPDATE_FORM_SELECTOR = '#PromotionRefreshAreaViewURL';

		this.BASE_URL = `https://www.${merchantDomain}`;
		this.BASE_REQUEST_URL = `${this.BASE_URL}/webapp/wcs/stores/servlet`;
		this.APPLY_CODE_END_POINT = '/AjaxRESTApplyPromoCodeForWidget';
		this.APPLY_URL = `${this.BASE_REQUEST_URL}${this.APPLY_CODE_END_POINT}`;

		this.EMPTY_SELECTOR = '.ECP-emptycart';

		this.GIFT_MSG_FORM_SELECTOR = '.pers-edit';

		this.getPureTotal = false;
	}

	watchForCartUrl() {
		const interval = super.watchForCartUrl();

		sessionTimer.initialize({
			actionInterval: interval,
			timeout: 15,
		});
	}

	checkCondition() {
		const { href } = window.location;
		const isOnHref =
			href.startsWith(`${this.BASE_REQUEST_URL}/FDRecipientsDisplayCmd`) ||
			href.startsWith(`${this.BASE_REQUEST_URL}/AjaxCheckoutDisplayView`) ||
			href.startsWith(`${this.BASE_REQUEST_URL}/FDPlaceOrderDisplayCmd`) ||
			href.startsWith(`${this.BASE_URL}/AjaxCheckoutDisplayView`);
		const price = document.querySelector(this.TOTAL_SELECTOR);
		const isInput = (document.querySelector(this.CODE_INPUT_SELECTOR) || {}).offsetParent;
		const isAppliedCodeMsgSelector = (document.querySelector(this.APPLIED_CODE_MSG_SELECTOR) || {}).offsetParent;
		const isEmpty = document.querySelector(this.EMPTY_SELECTOR);
		const shippingFormActive = document.querySelector(this.SHIPPING_FORM_SELECTOR);
		const giftMsgFormActive = document.querySelector(this.GIFT_MSG_FORM_SELECTOR);
		return (
			isOnHref &&
			(isInput || isAppliedCodeMsgSelector) &&
			!isEmpty &&
			price &&
			!shippingFormActive &&
			!giftMsgFormActive
		);
	}

	beforeTestCodes() {
		this.APPLY_FORM_DATA = extractFormValues(this.APPLY_FORM_SELECTOR);
		this.REMOVE_FORM_DATA = `/${document.querySelector(this.REMOVE_FORM_SELECTOR).getAttribute('value')}`;
	}

	getExistingCodes() {
		const codes = [document.querySelector(this.APPLIED_CODES_SELECTOR).getAttribute('value')].filter(Boolean);
		return { codes, codeCount: codes.length };
	}

	removeCodes(codes) {
		return this._removeCode(codes[0])
			.then(() => this._getUpdatedCart())
			.then(responseHTML => this._parseTotal(responseHTML));
	}

	checkCodes(codes, originalPrice = this._parseTotal()) {
		log(`Original price: ${originalPrice}`);

		return Promise.each(codes, code => {
			const result = { code, finalPrice: originalPrice, finalDiscount: 0, isValid: false };
			return this._applyCode(code).then(responseJSON => {
				if (!this._codeAppliedCheck(responseJSON)) {
					return bus.$emit('code-checked', result);
				}
				return this._getUpdatedCart().then(responseHTML => {
					result.finalPrice = this._parseTotal(responseHTML);
					result.finalDiscount = originalPrice - result.finalPrice;
					result.isValid = true;
					return this._removeCode(code).then(() => bus.$emit('code-checked', result));
				});
			});
		});
	}

	applyCodes(codes) {
		return this._applyCode(codes[0]);
	}

	getPreTaxShippingTotal() {
		return this.getPureTotal
			? this._getUpdatedCart().then(response => {
					const shipping = elemToFloat(this._findElement('Shipping', response)) || 0;
					// tax not always present on the page
					const taxEl = this._findElement('Estimated Tax', response);
					const tax = taxEl ? elemToFloat(taxEl) || 0 : 0;
					const shippingDiscountEl = this._findElement('Shipping Discount', response);
					const shippingDiscount = shippingDiscountEl ? elemToFloat(shippingDiscountEl) || 0 : 0;
					return this._parseTotal(response) - shipping - tax + shippingDiscount;
			  })
			: null;
	}

	_findElement(targetString, response) {
		const targetEl = [...extractElement(this.PRICES_SELECTOR, response, true)].find(el =>
			el.textContent.includes(targetString),
		);
		return targetEl ? targetEl.querySelector(this.PRICE_SELECTOR) : null;
	}

	_removeCode(code) {
		const removeURL = `${this.REMOVE_FORM_DATA}&needsToRemove=Promotion&promotionCode=${code}`;

		return this._manageCode(`${this.BASE_URL}${removeURL}`, 'POST');
	}

	_applyCode(code) {
		this.APPLY_FORM_DATA.params.set('taskType', 'A');
		this.APPLY_FORM_DATA.params.set('promoCode', code);
		this.APPLY_FORM_DATA.params.set('appliedPromoCode', code);
		return this._manageCode(this.APPLY_URL, 'POST', this.APPLY_FORM_DATA.params);
	}

	_manageCode(urlString, method, body) {
		return fetch(urlString, { method, credentials: 'include', body })
			.then(response => response.text())
			.then(response => {
				// Return JSON if it is possible
				try {
					this.lastResponse = JSON.parse(response);
				} catch (e) {
					this.lastResponse = response;
				}
				return this.lastResponse;
			});
	}

	_codeAppliedCheck(data) {
		return data.successFlag === 'true';
	}

	_getUpdatedCart() {
		return this._manageCode(`${this.BASE_URL}${window.location.pathname}`, 'GET');
	}

	_parseTotal(data = false) {
		return toFloat(extractElement(this.TOTAL_SELECTOR, data).getAttribute('value'));
	}
}
