import DriverTemplate from './_base';
import { format, DOM } from '../../../utils';

const { elemToFloat } = format;
const { extractElement, extractFormValues } = DOM;

/*
Driver behavior description:
    Existing code: yes
    Remove code: yes
    Stackable: no
    Free gift support: no
    'isValid' implemented: yes
     Codes can be replaced by another code: no
    Manually apply code: no
    Is driver SPA and add the condition to show popup: yes
    sessionTimer 15 min
    pages, where extension works: Cart, Checkout (support Checkout and clear data, because code input may not appear on Cart page)
    Gather codes on site pages: yes, from main page
    Special case: get Total price with shipping,
     so total and final price in console will be wrong, but discount will be right
*/

const DOMAIN = 'harryanddavid.com';

const Driver = class Driver extends DriverTemplate {
	constructor() {
		super(DOMAIN);

		this.BASE_URL = `https://www.${DOMAIN}`;

		this.MERCHANT_CODES_SELECTOR = '.hdTileHomepageFeatureA, #HD_HomePageESpot_14 a';
		this.MERCHANT_CODES_REGEXP = /use code (\w+)/i;

		this.TOTAL_SELECTOR = '.price-cart-summary';
		this.REMOVE_FORM_SELECTOR = '#AjaxRESTRemovePromoCodeForWidget';
		this.SHIPPING_FORM_SELECTOR = '.address_Modal';

		this.getPureTotal = true;
	}

	beforeTestCodes() {
		this.APPLY_FORM_DATA = extractFormValues(this.APPLY_FORM_SELECTOR);
		this.REMOVE_FORM_DATA = document.querySelector(this.REMOVE_FORM_SELECTOR).getAttribute('value');
		this.UPDATE_FORM_DATA = document.querySelector(this.UPDATE_FORM_SELECTOR).getAttribute('value');
	}

	getMerchantCodes() {
		return this._getMainPage().then(responseHTML => [
			...new Set(
				[...extractElement(this.MERCHANT_CODES_SELECTOR, responseHTML, true)]
					.map(elem => elem.innerText.match(this.MERCHANT_CODES_REGEXP))
					.filter(Boolean)
					.map(code => code[1]),
			),
		]);
	}

	_getMainPage() {
		return fetch(this.BASE_URL, {
			method: 'GET',
		}).then(response => response.text());
	}

	_getUpdatedCart() {
		return this._manageCode(`${this.BASE_URL}${this.UPDATE_FORM_DATA}`, 'POST');
	}

	_parseTotal(data = false) {
		return elemToFloat(
			extractElement(this.TOTAL_SIBLING_SELECTOR, data).previousElementSibling.querySelector(this.TOTAL_SELECTOR),
		);
	}
};

export default {
	name: 'Harry & David',
	domain: 'harryanddavid.com',
	Driver,
	isSinglePageApp: true,
};
